.size-h3,
.Content h3,
.Content .size-h3,
.wp-editor h3,
.wp-editor .size-h3,
.size-h4,
.Content h4,
.Content .size-h4,
.wp-editor h4,
.wp-editor .size-h4,
.size-h5,
.Content h5,
.Content .size-h5,
.wp-editor h5,
.wp-editor .size-h5,
.size-h6,
label,
.Content h6,
.Content .size-h6,
.wp-editor h6,
.wp-editor .size-h6 {
  line-height: 1.5;
}

.size-hero,
.size-h1,
.Content h1,
.Content .size-h1,
.wp-editor h1,
.wp-editor .size-h1,
.size-h2,
.Content h2,
.Content .size-h2,
.wp-editor h2,
.wp-editor .size-h2 {
  line-height: 1.2;
}

.size-hero {
  font-size: 38px;
  font-size: calc( 38px + (48 - 38) * ( (100vw - 400px) / ( 800 - 400) ));
}

.size-h1,
.Content h1,
.Content .size-h1,
.wp-editor h1,
.wp-editor .size-h1 {
  font-size: 36px;
  font-size: calc( 36px + (48 - 36) * ( (100vw - 400px) / ( 800 - 400) ));
}

.size-h2,
.Content h2,
.Content .size-h2,
.wp-editor h2,
.wp-editor .size-h2 {
  font-size: 32px;
  font-size: calc( 30px + (32 - 30) * ( (100vw - 400px) / ( 800 - 400) ));
}

.size-h3,
.Content h3,
.Content .size-h3,
.wp-editor h3,
.wp-editor .size-h3 {
  font-size: 26px;
  font-size: calc( 26px + (28 - 26) * ( (100vw - 400px) / ( 800 - 400) ));
}

.size-h4,
.Content h4,
.Content .size-h4,
.wp-editor h4,
.wp-editor .size-h4 {
  font-size: 20px;
  font-size: calc( 20px + (24 - 20) * ( (100vw - 400px) / ( 800 - 400) ));
}

.size-h5,
.Content h5,
.Content .size-h5,
.wp-editor h5,
.wp-editor .size-h5 {
  font-size: 16px;
  font-size: calc( 14px + (16 - 14) * ( (100vw - 400px) / ( 800 - 400) ));
}

.size-h6,
label,
.Content h6,
.Content .size-h6,
.wp-editor h6,
.wp-editor .size-h6 {
  font-size: 14px;
  font-size: calc( 12px + (14 - 12) * ( (100vw - 400px) / ( 800 - 400) ));
}

.size-p,
.Content,
.wp-editor,
.Content p,
.wp-editor p {
  font-size: 16px;
  font-size: calc( 16px + (19 - 16) * ( (100vw - 400px) / ( 800 - 400) ));
}

